import React, { Component } from "react";
import axios from 'axios';

import SearchLine from './search-line'
import Characters from './characters'
import ListNavigator from './list-navigator'
import LoadingScreen from './loading-screen'

export default class PeopleSearchEngine extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pageIsLoading: false,
      pageCounter: 1,
      charactersList: [],
      previousRequestHttp: null,
      nextRequestHttp: null,
      requestHttp: "https://swapi.co/api/people/"
    }

    this.changePage = this.changePage.bind(this);
    this.leftArrow = this.leftArrow.bind(this);
    this.rightArrow = this.rightArrow.bind(this);
  }
  leftArrow() {
    this.setState({ pageCounter: this.state.pageCounter-1 });
    this.changePage(this.state.previousRequestHttp);
  }
  rightArrow() {
    this.setState({ pageCounter: this.state.pageCounter+1 });
    this.changePage(this.state.nextRequestHttp);
  }
  changePage(http) {
    this.setState({
      pageIsLoading: true,
    })
    axios.get(http)
    .then(response => { 
      this.setState({
        pageIsLoading: false,
        charactersList: [...response.data.results],
        previousRequestHttp: response.data.previous,
        nextRequestHttp: response.data.next
      });
    })
    .catch(function(error) {
      console.log(error);
    });
  }

  componentDidMount() {
    this.setState({
      pageIsLoading: true,
    });
    axios.get(this.state.requestHttp)
      .then(response => { 
        this.setState({
          pageIsLoading: false,
          charactersList: [...response.data.results],
          previousRequestHttp: response.data.previous,
          nextRequestHttp: response.data.next
        });
      })
      .catch(function(error) {
        console.log(error);
      });
  }
  render() {
    const loadingScreen = this.state.pageIsLoading && <LoadingScreen />
    return (
      <div className="people-search-engine">
        {loadingScreen}
        <SearchLine />
        <Characters charactersList={this.state.charactersList}/>
        <ListNavigator 
          previousRequestHttp={this.state.previousRequestHttp}
          nextRequestHttp={this.state.nextRequestHttp}
          leftArrow={this.leftArrow}
          rightArrow={this.rightArrow}
          pageCounter={this.state.pageCounter}
        />
      </div>
    );
  }
}

