import React, { Component } from "react";

import arrow from "../arrow.png"

export default class ListNavigator extends Component {
    render() {
        const {previousRequestHttp, nextRequestHttp, leftArrow, rightArrow, pageCounter} = this.props;

        const left = previousRequestHttp!==null && <React.Fragment> 
                <button onClick={leftArrow}> 
                    <img src={arrow} height="20" alt="ops"  className="left-arrow"/>
                </button>
                <button className="square-navigator" onClick={leftArrow}>{pageCounter-1}</button>
            </React.Fragment>

        const right = nextRequestHttp!==null && <React.Fragment> 
                <button className="square-navigator" onClick={rightArrow}>{pageCounter+1}</button>
                <button onClick={rightArrow}>
                    <img src={arrow} height="20" alt="ops" className="right-arrow"/>
                </button>
            </React.Fragment>

        return (
            <div className="navigator">
                {left}
                <button className="square-navigator-center">{pageCounter}</button>
                {right}
            </div>
        );
    }
}